.grid {
  padding: 50px 20px;
  @media (min-width: 768px) {
    padding: 120px 80px;
  }
}
#news-waterfall {
  column-count: 1;
  column-gap: 20px;
  word-wrap: break-word;
  width: 100%;
  margin: auto;
  padding-bottom: 40px;
  @media (min-width: 768px) {
    column-count: 2;
    column-gap: 30px;
  }
  @media (min-width: 992px) {
    column-count: 3;
    column-gap: 50px;
  }
}

