#news-waterfall .box {
  position: relative;
  box-shadow: 3px 0px 10px 0px rgba(33, 33, 33, 0.378);
  margin-bottom: 50px;
  overflow: hidden;
}
#news-waterfall .box img {
  width: 100%;
  display: block;
  background-size: cover;
}
.button-top,
.button-bottom {
  position: absolute;
  right: 10px;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.button-top {
  top: 10px;
}
.button-bottom {
  bottom: 10px;
}
.box:hover .button-top,
.box:hover .button-bottom {
  opacity: 1;
}
.icon {
  font-size: 20px;
  cursor: pointer;
  color: white;
  background-color: rgb(242, 239, 239);
  border-radius: 10%;
  margin: 5px;
  color: gray;
  transition: 0.3s linear;
}
.download-icon {
  padding: 5px 20px;
}
.download-icon:hover {
  color: rgba(0, 0, 0, 0.757);
}
.heart-icon {
  padding: 10px;
}
.heart-icon:hover {
  color: rgba(255, 0, 0, 0.841);
}
.showMore {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}
.showMoreButton {
  background-color: transparent;
  color: #000000;
  border: 1px solid #000000;
  border-radius: 5px;
  padding: 10px 30px;
  cursor: pointer;
  font-size: 16px;
  transition: 0.3s linear;
}
.showMoreButton:hover {
  background-color: #000;
  color: #fff;
}