/* Navigation bar */
.logo-img {
  width: 120px;
}

#header {
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 80px;
  max-height: 120px;
  background-color: transparent;
}

#navbar {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

#navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}

#navbar li a {
  text-decoration: none;
  font-size: 18px;
  color: #dad7d7;
  transition: 0.3s ease;
}

.login-button {
  background-color: #008060;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  transition: 0.5s ease;
}

.login-button:hover {
  background-color: #00a47b;
}

.upload-button {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
}

.upload-button:hover {
  background-color: #fff;
  color: #000;
}

.left-border {
  border-left: solid 2px #fff;
}

.dropbtn {
  color: white;
  padding: 12px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.dropdown-content {
  display: none;
}

.column a {
  color: black;
  padding: 12px 16px;
  width: 150px;
  display: block;
  transition: 0.5s ease;
}

.arrow {
  font-size: 10px;
  margin-left: 8px;
  vertical-align: middle;
}

.dropdown.clicked .dropdown-content {
  margin-top: 1rem;
  left: -90px;
  border-radius: 5px;
  position: absolute;
  background-color: #302c2c;
  min-width: 360px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

.dropdown-content .column li:hover {
  text-decoration: underline;
  color: #dad7d7;
}

.column {
  text-align: center;
}

.hide-desktop-menu {
  display: none;
}

.mobile-menu {
  display: none;
}

.mobile-menu a {
  text-decoration: none;
}

.mobile-menu.active {
  position: absolute;
  top: 0;
  right: 0;
  width:190px;
  padding: 20px;
  background-color: rgba(80, 80, 80, 0.37);
  border-bottom-left-radius: 3px;
  text-align: center;
  font-size: 20px;
}


.mobile-menu-button {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  color: #dad7d7;
  font-size:35px;
}

.mobile-menu-button.active {
    position: relative;
    top: -40px;
    right: 120px;
}


@media (max-width: 1100px) {
  .mobile-menu-button {
   display: flex;
  }

  .mobile-menu {
    display: none;
  }

  .mobile-menu li {
    padding: 6px;
    list-style: none;
  }

  .desktop-menu {
    display: none;
  }

  .mobile-menu {
    display: none;
  }

  .mobile-menu.active {
    display: block;
  }
}

@media (max-width: 768px) {
  #header .logo{
    margin-left: -40px;
  }

  .mobile-menu.active {
    position: absolute;
    top: 0;
    right: 0;
    width:150px;
    padding: 20px;
    background-color: rgba(80, 80, 80, 0.73);
    border-bottom-left-radius: 3px;
    text-align: center;
    font-size: 20px;
  }

  .mobile-menu.active li {
    padding: 10px 0;
  }

  .mobile-menu-button {
    font-size:30px;
    margin-right: -40px;
  }
  
  .mobile-menu-button.active {
      position: relative;
      top: -40px;
      right: 120px;
  }
}
